html,
body,
#main,
body > .main {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: arial;
}

.test-title {
  font-weight: normal;
  font-size: 16px;
}
.test-title-inner {
  display: inline-block;
  *display: inline;
  zoom: 1;
  text-align: left;
}
.test-title strong {
  color: yellow;
  font-weight: 700;
  text-shadow: -1px 0 #000, 0 1px #000, 1px 0 #000, 0 -1px #000;
  padding-left: 2px;
  padding-right: 2px;
}
.test-buttons button {
  margin: 10px 5px;
}
.test-chart-block {
  position: relative;
}
.test-chart-block-has-right {
  overflow: hidden;
}
.test-chart-block-has-right .test-chart-block-right {
  position: absolute;
  right: 10px;
  background: #fff;
  z-index: 99;
  width: 300px;
  max-height: 99%;
  border-left: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.test-chart-block-has-right .test-chart-block-left {
  margin-right: 320px;
}
.test-info {
  padding-left: 10px;
  overflow: auto;
}
pre.test-print-object {
  font-size: 12px;
  font-family: Menlo, Monaco, "Courier New", monospace;
}
.test-chart {
  height: 400px;
}

.test-data-table {
  position: relative;
  text-align: center;
}
.test-data-table table {
  display: inline-block;
  vertical-align: top;
  border: 1px solid #ccc;
  border-spacing: 0;
  margin: 30px 15px;
}
.test-data-table td {
  border: 1px solid #ccc;
  color: #777;
  padding: 3px 5px;
  font-size: 13px;
}
td.test-data-table-key {
  font-size: 12px;
  color: rgb(69, 162, 238);
}

.record-canvas .content-area {
  display: none;
  position: absolute;
  background: #fff;
  left: 10px;
  top: 20px;
  border: 2px solid #000;
  padding: 10px;
  z-index: 9999;
  box-shadow: 0 0 3px #000;
}
.record-canvas textarea {
  width: 300px;
  height: 500px;
}

.control-frame-btn-panel {
  position: fixed;
  top: 10px;
  left: 10px;
  box-shadow: 0 0 3px #000;
  background: green;
  padding: 5px;
}
.control-frame-btn-panel .control-frame-info {
  display: block;
  color: #fff;
  font-size: 10px;
}

.marginL-20 {
  margin-left: 20px;
}

.marginL-25 {
  margin-left: 25px;
}

.marginL-10 {
  margin-left: 10px;
}

.marginL-30 {
  margin-left: 30px;
}

.marginT-10 {
  margin-top: 10px;
}

.doc-nav > {
  .nav-item {
    .sub-menu {
      .nested-sub-menu {
        background-color: unset !important;
        margin-top: 0px;
        padding-left: 15px;
        li {
          + li {
            margin-top: 0px;
            padding: 0px;
          }
          a {
            padding-top: 10px;
          }
        }
      }
      .nav-link {
        &:hover {
          background-color: unset;
        }
      }
    }
  }
}

.doc-nav {
  .nav-item {
    &.active {
      .sub-menu {
        .nav-item {
          .title {
            background-color: unset !important;
            outline: none;
            padding: 0;
            .nav-link {
              color: #59667a !important;
              font-weight: 500;
            }
          }
        }
      }
    }
  }
}

.backend-installation-steps-container h3 {
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 15px;
}

